'use client';

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { mockAnomaService, MockWalletConnection, MockUser } from '@/lib/mockAnomaService';

interface WalletConnectionProps {
  onConnectionChange?: (connected: boolean, user?: MockUser) => void;
}

export default function WalletConnection({ onConnectionChange }: WalletConnectionProps) {
  const [wallet, setWallet] = useState<MockWalletConnection | null>(null);
  const [user, setUser] = useState<MockUser | null>(null);
  const [isConnecting, setIsConnecting] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
    // Check if wallet is already connected
    const existingWallet = mockAnomaService.getWalletConnection();
    const existingUser = mockAnomaService.getCurrentUser();
    
    if (existingWallet && existingUser) {
      setWallet(existingWallet);
      setUser(existingUser);
      onConnectionChange?.(true, existingUser);
    }
  }, [onConnectionChange]);

  const handleConnect = async () => {
    setIsConnecting(true);
    setError(null);

    try {
      const connectedWallet = await mockAnomaService.connectWallet();
      const currentUser = mockAnomaService.getCurrentUser();
      
      setWallet(connectedWallet);
      setUser(currentUser);
      onConnectionChange?.(true, currentUser || undefined);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to connect wallet');
    } finally {
      setIsConnecting(false);
    }
  };

  const handleDisconnect = async () => {
    try {
      await mockAnomaService.disconnectWallet();
      setWallet(null);
      setUser(null);
      onConnectionChange?.(false);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to disconnect wallet');
    }
  };

  if (!mounted) {
    return null; // Prevent hydration mismatch
  }

  if (wallet && user) {
    return (
      <div className="glass-card rounded-3xl p-8 animate-scale-in-center">
        <div className="text-center mb-8">
          <div className="relative inline-block">
            <div className="w-20 h-20 bg-gradient-to-br from-green-500 to-green-600 rounded-3xl flex items-center justify-center mb-6 mx-auto shadow-2xl">
              <div className="w-3 h-3 bg-white rounded-full animate-pulse"></div>
            </div>
            <div className="absolute -inset-1 bg-gradient-to-br from-green-500 to-green-600 rounded-3xl blur opacity-20 animate-pulse"></div>
          </div>
          <h3 className="text-2xl font-black text-gray-900 mb-2 uppercase tracking-wide">
            Wallet Connected
          </h3>
          <p className="text-gray-600 font-medium">
            Successfully connected to Anoma network
          </p>
        </div>

        <div className="space-y-6">
          <div className="bg-gradient-to-br from-gray-50 to-white rounded-2xl p-6 border border-gray-100">
            <div className="text-sm text-gray-500 font-semibold uppercase tracking-wide mb-2">Wallet Address</div>
            <div className="font-mono text-lg text-gray-900 bg-white rounded-xl p-4 border border-gray-200 shadow-inner">
              {wallet.address.slice(0, 6)}...{wallet.address.slice(-4)}
            </div>
          </div>
          
          <div className="bg-gradient-to-br from-gray-50 to-white rounded-2xl p-6 border border-gray-100">
            <div className="text-sm text-gray-500 font-semibold uppercase tracking-wide mb-2">Network</div>
            <div className="text-lg font-bold text-green-600 uppercase tracking-wide">{wallet.network}</div>
          </div>

          {user.username && (
            <div className="bg-gradient-to-br from-gray-50 to-white rounded-2xl p-6 border border-gray-100">
              <div className="text-sm text-gray-500 font-semibold uppercase tracking-wide mb-2">Username</div>
              <div className="text-lg font-bold text-gray-900">{user.username}</div>
            </div>
          )}

          <div className="pt-4">
            <Button 
              onClick={handleDisconnect}
              className="w-full bg-gradient-to-r from-gray-500 to-gray-600 hover:from-gray-600 hover:to-gray-700 text-white font-bold py-4 px-8 rounded-2xl text-lg uppercase tracking-wide transition-all duration-300 hover:scale-105 hover:shadow-xl"
            >
              Disconnect
            </Button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="glass-card rounded-3xl p-8 animate-scale-in-center">
      <div className="text-center mb-8">
        <div className="relative inline-block">
          <div className="w-20 h-20 bg-gradient-to-br from-red-500 to-red-600 rounded-3xl flex items-center justify-center mb-6 mx-auto shadow-2xl pulse-modern">
            <div className="w-10 h-10 border-3 border-white rounded-2xl"></div>
          </div>
          <div className="absolute -inset-1 bg-gradient-to-br from-red-500 to-red-600 rounded-3xl blur opacity-20"></div>
        </div>
        <h3 className="text-2xl font-black text-gray-900 mb-2 uppercase tracking-wide">
          Connect Your Anoma Wallet
        </h3>
        <p className="text-gray-600 font-medium text-lg">
          Connect your wallet to access the decentralized skill exchange network
        </p>
      </div>

      {error && (
        <div className="mb-8 animate-fade-in-up">
          <div className="bg-gradient-to-r from-red-50 to-red-100 border-2 border-red-200 rounded-2xl p-6">
            <div className="text-red-700 font-semibold text-center">{error}</div>
          </div>
        </div>
      )}

      <div className="space-y-8">
        <div className="bg-gradient-to-br from-gray-50 to-white rounded-2xl p-6 border border-gray-100">
          <div className="text-lg font-bold text-gray-900 mb-4 uppercase tracking-wide">
            By connecting your wallet, you'll be able to:
          </div>
          <ul className="space-y-4 text-gray-600 font-medium">
            <li className="flex items-center gap-4">
              <div className="w-3 h-3 bg-gradient-to-r from-green-500 to-green-600 rounded-full flex-shrink-0"></div>
              <span className="text-lg">Post teaching and learning intents</span>
            </li>
            <li className="flex items-center gap-4">
              <div className="w-3 h-3 bg-gradient-to-r from-green-500 to-green-600 rounded-full flex-shrink-0"></div>
              <span className="text-lg">Get matched with compatible peers</span>
            </li>
            <li className="flex items-center gap-4">
              <div className="w-3 h-3 bg-gradient-to-r from-green-500 to-green-600 rounded-full flex-shrink-0"></div>
              <span className="text-lg">Send encrypted messages</span>
            </li>
            <li className="flex items-center gap-4">
              <div className="w-3 h-3 bg-gradient-to-r from-green-500 to-green-600 rounded-full flex-shrink-0"></div>
              <span className="text-lg">Control your privacy settings</span>
            </li>
          </ul>
        </div>

        <Button 
          onClick={handleConnect}
          disabled={isConnecting}
          className="btn-modern-primary w-full"
        >
          {isConnecting ? (
            <div className="flex items-center gap-3">
              <div className="w-6 h-6 border-3 border-white border-t-transparent rounded-full animate-spin"></div>
              <span>CONNECTING...</span>
            </div>
          ) : (
            'CONNECT ANOMA WALLET'
          )}
        </Button>

        <div className="text-center">
          <div className="bg-gradient-to-r from-blue-50 to-indigo-50 rounded-2xl p-6 border border-blue-100">
            <div className="text-sm text-blue-700 font-medium">
              <span className="font-bold">Demo Mode:</span> This is a demo using mock wallet functionality. 
              In production, this would connect to your actual Anoma wallet.
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
