// Mock Anoma Service for Skill Exchange Marketplace
// This simulates blockchain functionality for development/testing

export interface MockWalletConnection {
  address: string;
  connected: boolean;
  network: string;
}

export interface MockIntent {
  id: string;
  userId: string;
  intentType: 'teach' | 'learn';
  skillName: string;
  skillLevel: 'beginner' | 'intermediate' | 'advanced' | 'expert';
  availability?: string;
  location?: string;
  description?: string;
  status: 'active' | 'matched' | 'completed' | 'cancelled';
  createdAt: string;
}

export interface MockMatch {
  id: string;
  teacherIntentId: string;
  learnerIntentId: string;
  teacherUser: MockUser;
  learnerUser: MockUser;
  skill: string;
  status: 'pending' | 'accepted' | 'rejected' | 'completed';
  compatibilityScore: number;
  matchReasons: string[];
  createdAt: string;
}

export interface MockUser {
  id: string;
  walletAddress: string;
  username?: string;
  email?: string;
  bio?: string;
  location?: string;
  avatarUrl?: string;
  privacySettings: {
    showEmail: boolean;
    showLocation: boolean;
    showRealName: boolean;
    showBio: boolean;
    allowDirectMessages: boolean;
  };
}

export interface MockMessage {
  id: string;
  matchId: string;
  senderId: string;
  message: string;
  encrypted: boolean;
  timestamp: string;
}

